.page-header {
  background-color: fade(#000000, 30%);
  color: #ffffff;
}

.page-header__logo {
  height: 23px;
}


.page-header__wrapper {
  display: flex;
  justify-content: space-between;
}

@media(min-width: @desktop-width) {
  .page-header__wrapper {
    width: 960px;
    margin: 0 auto;
  }
}