.download {
  background-color: #1d2631;
  color: #ffffff;
}

.download__wrapper {
  padding: 0 20px;

  @media(min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
  }
  
  @media(min-width: @desktop-width) {
    max-width: 960px;
    margin: auto;
  }  
}

@media(min-width: @tablet-width) {
  .download__img {
    order: 1;
  }
}


.download__slogan {
  margin: 0;
}

.download__legend {
  margin: 0;
}

.download__platform a {
  color: #ffffff;
}

.download__slogan--top {
  @media(min-width: @mobile-width-only ) {
    display: none;
  }
}

.download__slogan--bottom {
  @media(max-width: @tablet-width) {
    display: none;
  }
}